.footer {
  position: relative;
  top: 0px;
  left: 0px;

  &__container {
    @include meduim-tablet {
      display: flex;
      flex-direction: column;
    }
  }

  &__top {
    padding-top: 178px;
    padding-bottom: 80px;
    background-color: #1a3e3e;

    @include small-tablet {
      padding-top: 50px;
    }
  }

  &__left {
    grid-column: 5 span;

    @include tablet {
      grid-column: 3 span;
    }
  }

  &__name {
    margin-bottom: 80px;

    @include meduim-tablet {
      margin-bottom: 30px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 17px;
    color: #F3F5F4;
    font-size: 20px;
    font-weight: 700;
    line-height: 120%;

    &--services {
      margin-bottom: 20px;
    }
  }

  &__description {
    color: var(--white-color);
    font-size: 14px;
    line-height: 142.857%;
  }

  &__links {
    margin-bottom: 32px;
  }

  &__email {
    margin-right: 24px;
    color: var(--white-color);
    font-size: 14px;
    font-weight: 600;
    line-height: 128.571%;
    text-decoration-line: underline;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--main-color-light-hover);
    }

    &:active {
      color: var(--main-color-light-active);
    }
  }

  &__number {
    color: var(--white-color);
    font-size: 14px;
    font-weight: 600;
    line-height: 128.571%;
    text-decoration-line: underline;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--main-color-light-hover);
    }

    &:active {
      color: var(--main-color-light-active);
    }
  }

  &__center {
    grid-column: 3 span;

    @include tablet {
      grid-column: 3 span;
    }
  }

  &__right {
    grid-column: 10 / 13;

    @include tablet {
      grid-column: 2 span;
    }
  }

  &__bottom {
    background-color: #18121e;
    padding: 24px 0;
  }

  &__copy {
    justify-content: space-between;
    align-items: center;

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__down {
    margin: 0;
    color: var(--white-color);
    font-size: 14px;
    font-weight: 600;
    line-height: 128.571%;
  }

  &__ip {
    @include small-tablet {
      margin-bottom: 8px;
    }
  }

  &__policy {
    text-decoration: underline;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--main-color-light-hover);
    }

    &:active {
      color: var(--main-color-light-active);
    }
  }
}

.social {

  @include meduim-tablet {
    margin-bottom: 50px;
  }

  &__item {
    margin-right: 16px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    svg {
      path {
        fill: #fff;
        transition: fill 0.3s ease-in-out;
      }
    }

    &:focus {
      outline: none;
    }

    @include hover {
      svg {
        path {
          fill: var(--main-color-light-hover);
        }
      }
    }

    &:active {
      svg {
        path {
          fill: var(--main-color-light-active);
        }
      }
    }
  }
}

.services {

  @include meduim-tablet {
    margin-bottom: 50px;
  }

  &__item {
    margin-bottom: 16px;
  }

  &__link {
    background: url(../img/footer/ico.svg) left center no-repeat;
    padding-left: 17px;
    color: var(--main-color-light);
    font-size: 14px;
    line-height: 128.571%;
    transition: color 0.3s ease-in-out, background 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      background: url(../img/footer/ico-ye.svg) left center no-repeat;
      color: var(--main-color-yellow);
    }

    &:active {
      outline: 1.5px solid var(--main-color-light);
    }
  }
}

.adventure {
  &__item {
    margin-bottom: 16px;
    background: url(../img/footer/ico.svg) left center no-repeat;
    padding-left: 17px;
    transition: background 0.3s ease-in-out;

    @include hover {
      background: url(../img/footer/ico-ye.svg) left center no-repeat;
    }
  }

  &__link {
    color: var(--main-color-light);
    font-size: 14px;
    line-height: 128.571%;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--main-color-yellow);
    }

    &:active {
      outline: 1.5px solid var(--main-color-light);
    }
  }
}


.services__link.focus-visible {
  outline: 1.5px solid var(--main-color-light);
}

.social__link.focus-visible {
  svg {
    path {
      fill: var(--main-color-light-active);
    }
  }
}

.adventure__link.focus-visible {
  outline: 1.5px solid var(--main-color-light);
}


.footer__policy.focus-visible {
  outline: 1.5px solid var(--main-color-light);
}

.footer__email.focus-visible {
  outline: 1.5px solid var(--main-color-light);
}

.footer__number.focus-visible {
  outline: 1.5px solid var(--main-color-light);
}
