.mailing {
  position: relative;
  top: 98px;
  left: 0;
  z-index: 15;

  @include small-tablet {
    top: 0;
    margin-bottom: 50px;
  }

  &__container {
    column-gap: 64px;
    padding: 40px 100px;
    border-radius: 6px;
    background: var(--main-color-light);
    box-shadow: 0px 0px 15px 0px rgba(24, 18, 30, 0.12);

    @include tablet {
      flex-direction: column;
      padding: 24px;
    }
  }

  &__name {
    flex: 0 1 40%;

    @include tablet {
      margin-bottom: 25px;
    }

    @include small-tablet {
      align-self: start;
    }
  }

  &__subtitle {
    font-size: 18px;
    line-height: 111.111%;
    margin-bottom: 8px;

    @include tablet {
      text-align: center;
    }

    @include small-tablet {
      text-align: initial;
    }
  }

  &__title {
    font-size: 26px;

    @include tablet {
      text-align: center;
    }

    @include small-tablet {
      text-align: initial;
    }
  }

  &__form {
    flex: 1 1 60%;
    flex-direction: column;
  }

  &__wrapper {
    align-items: flex-end;

    @include tablet {
      align-items: flex-start;
      flex-direction: column;
      margin-bottom: 24px;
    }
  }

  &__label {
    flex-direction: column;
    margin-right: 16px;

    span {
      margin-bottom: 8px;
    }

    @include tablet {
      margin-bottom: 16px;
      width: 100%;
    }
  }

  &__btn {
    height: max-content;
  }

  &__input {
    border-radius: 4px;
    background: #F3F5F4;
    padding: 12px 16px;
    width: 280px;
    height: 44px;

    @include tablet {
      width: 100%;
    }

    &::placeholder {
      color: var(--main-color-dark);
      font-size: 14px;
      font-weight: 300;
      line-height: 142.857%;
    }

    &:focus {
      outline: none;
    }
  }

  &__span {
    margin-top: 8px;
    color: var(--main-color-dark);
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;

    &--small {
      line-height: 128.571%;
      font-size: 14px;
    }
  }

  &__link {
    color: var(--main-color-dark);
    text-decoration-line: underline;

    &:focus {
      outline: none;
    }

    @include hover {
      color: #18121e9d;
    }

    &:active {
      color: #18121e59;
    }
  }
}

.mailing__input.focus-visible {
  outline: 1.5px solid var(--main-color-btn);
}

.mailing__link.focus-visible {
  outline: 1.5px solid var(--main-color-btn);
}
