.coast {
  background-color: #f3f5f4;
  padding: 80px 0 100px 0;
  margin-bottom: 80px;

  @include small-tablet {
    padding-bottom: 50px;
    margin-bottom: 50px;
  }

  &__left {
    grid-column: 6 span;

    @include notebook {
      grid-column: 12 span;
    }

    @include tablet {
      grid-column: 8 span;
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    @include small-tablet {
      grid-column: 6 span;
    }
  }

  &__name {
    margin-bottom: 40px;
    align-self: start;
  }

  &__descr {
    margin-bottom: 40px;
  }

  &__list {
    margin-bottom: 40px;
    width: 100%;
  }

  &__item {
    margin-bottom: 40px;
    align-items: center;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__image {
    margin-right: 16px;

    img {
      max-width: 50px;
      max-height: 50px;
    }
  }

  &__subtitle {
    margin-bottom: 8px;
  }

  &__label {
    color: var(--main-color-dark);
    font-size: 18px;
    font-weight: 700;
    line-height: 111.111%;
    letter-spacing: 0.216px;
  }

  &__description {
    color: var(--black-color);
    font-size: 14px;
    line-height: 142.857%;
  }

  &__right {
    width: 100%;
    height: max-content;
    padding-top: 20px;
    grid-column: -1 / 7;
    flex-wrap: wrap;
    justify-content: end;
    gap: 10px;

    @include notebook {
      display: none;
    }
  }

  &__picture {
    max-width: 250px;
    max-height: 250px;
    img {
      max-width: 250px;
      max-height: 250px;
    }
  }
}
