.header {
  padding: 16px 0;

  .logo {
    flex-shrink: 0;
  }

  &--position {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
  }

  &__container {
    justify-content: space-between;
    align-items: center;
  }

  &__buttons {
    align-items: center;

    @include tablet {
      padding-top: 30px;
      flex-direction: column;
      align-items: flex-end;
    }
  }

  &__button {
    @include small-tablet {
      display: none;
    }
  }
}

.nav {
  @include tablet {
    margin-bottom: 16px;
  }

  @include small-tablet {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    padding: 80px 16px;
    background-color: var(--main-color-btn);
    z-index: 100;
    transform: translateX(-100%);
    visibility: hidden;
    transition: transform 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  &__list {
    margin-right: 49px;
    width: 95%;

    @include tablet {
      justify-content: space-between;
    }

    @include small-tablet {
      flex-direction: column;
      overflow-y: auto;
      word-wrap: nowrap;
      padding: 10px 0;
      margin-right: 0;
    }
  }

  &__item {
    margin-right: 20px;

    @include notebook {
      margin-right: 10px;
    }

    @include tablet {
      margin-right: 20px;
    }

    @include meduim-tablet {
      margin-right: 15px;
    }

    @include small-tablet {
      margin-right: 0;
      margin-bottom: 16px;
    }

    &:last-child {
      margin: 0;
    }

  }

  &__link {
    color: var(--main-color-light);
    font-size: 18px;
    font-weight: 600;
    transition: color 0.3s ease-in-out;
    padding-left: 16px;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--main-color-light-hover);
    }

    &:active {
      color: var(--main-color-light-active);
    }

    @include notebook {
      font-size: 16px;
    }

    @include tablet {
      font-size: 16px;
      padding-left: 0;
    }

    @include small-tablet {
      background: url(../img/header/ico.svg) left center no-repeat;
      font-size: 18px;
      font-weight: 400;
      line-height: 128.571%;

      @include hover {
        padding-left: 16px;
        background: url(../img/footer/ico-ye.svg) left center no-repeat;
        color: var(--main-color-yellow);
        text-decoration: underline;
      }

      &:active {
        outline: 1.5px solid var(--main-color-light);
      }
    }
  }
}

.nav__link.focus-visible {
  outline: 1.5px solid var(--main-color-btn);

  @include small-tablet {
    padding-left: 16px;
    background: url(../img/footer/ico-ye.svg) left center no-repeat;
    color: var(--main-color-yellow);
    text-decoration: underline;
  }
}

.nav.menu--active {
  visibility: visible;
  transform: none;
}
