.blog {
  background-color: #f3f5f4;
  padding: 80px 0 100px 0;
  margin-bottom: 80px;

  &__container {
    flex-direction: column;
  }

  &__subtitle {
    margin-bottom: 8px;
    text-align: center;

    @include small-tablet {
      text-align: initial;
    }
  }

  &__title {
    text-align: center;

    @include small-tablet {
      text-align: initial;
    }
  }

  &__name {
    flex-direction: column;
    align-items: center;
    margin-bottom: 40px;

    @include small-tablet {
      align-items: start;
      align-self: start;
    }
  }

  &__article {
    flex-direction: column;
    align-items: center;
  }

  &__list {
    align-self: start;
    margin-bottom: 50px;
    gap: 20px;
    width: 100%;
    overflow-x: auto;

    @include small-tablet {
      display: flex;
      scroll-snap-type: x;
      align-self: start;

      &::-webkit-scrollbar {
        display: none;
      }
    }
  }

  &__item {
    grid-column: 6 span;
    align-items: center;
    column-gap: 24px;
    border-radius: 10px;
    background-color: var(--main-color-light);
    padding: 16px;
    scroll-snap-align: start;

    @include notebook {
      grid-column: 12 span;
    }

    @include tablet {
      grid-column: 8 span;
    }

    @include small-tablet {
      flex-direction: column;
      padding: 0;
      min-width: 285px;
    }
  }

  &__left {
    flex-shrink: 0;

    @include small-tablet {
      width: 100%;
      height: 280px;
    }
  }

  &__right {
    padding: 16px;

    & > * {
      margin-bottom: 24px;
    }

    &:last-child {
      margin-bottom: 0;
    }

    @include small-tablet {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
  }

  &__image {
    img {
      width: 230px;
      height: 280%;
    }

    @include small-tablet {
      width: 100%;
      height: 280px;

      img {
        width: 100%;
        height: 100%;
      }
    }
  }

  &__label {
    color: var(--main-color-dark);
    font-size: 24px;
    font-weight: 700;
    line-height: 133.333%;
    display: flex;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      text-decoration: underline;
    }

    &:active {
      text-decoration: underline;
      color: var(--main-color-dark-active);
    }

    @include small-tablet {
      flex: 0 1 auto;
    }
  }

  &__description {
    color: var(--main-color-dark);
    font-size: 14px;
    line-height: 142.857%;
    overflow: hidden;
    text-overflow: ellipsis;
    max-height: 116px;

    @include small-tablet {
      flex: 1 1 auto;
      max-height: 106px;
    }
  }

  &__info {
    justify-content: space-between;
    align-items: center;

    @include small-tablet {
      flex: 0 1 auto;
      margin-bottom: 0;
    }
  }

  &__date {
    color: #F2BE22;
    font-size: 14px;
    font-weight: 700;
    line-height: 142.857%;
    padding-right: 10px;
  }

  &__link {
    color: var(--main-color-btn);
    font-size: 14px;
    font-weight: 700;
    line-height: 142.857%;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      text-decoration: underline;
    }

    &:active {
      text-decoration: underline;
      color: var(--main-color-dark);
    }
  }
}

.blog__label.focus-visible {
  outline: 1.5px solid var(--main-color-btn);
}

.blog__link.focus-visible {
  outline: 1.5px solid var(--main-color-btn);
}
