.photo {
  padding-bottom: 100px;

  @include tablet {
    padding-bottom: 75px;
  }

  @include small-tablet {
    padding-bottom: 50px;
  }

  &__container {
    flex-direction: column;
    align-items: center;
  }

  &__subtitle {
    margin-bottom: 8px;
  }

  &__name {
    margin-bottom: 40px;
    flex-direction: column;
    align-items: center;

    @include tablet {
      align-items: start;
      align-self: start;
    }
  }

  &__images {
    width: 100%;
    align-self: start;
    margin-bottom: 50px;
    overflow-x: auto;

    @include tablet {
      display: flex;
      scroll-snap-type: y;

      &::-webkit-scrollbar {
        display: none;
      }
    }
  }

  &__image {
    grid-column: 3 span;
    scroll-snap-align: start;

    &--big {
      grid-column: 6 span;
    }

    @include tablet {
      min-width: 280px;
      min-height: 250px;

      img {
        height: 100%;
      }
    }
  }
}
