.rate {
  padding-bottom: 100px;

  &__container {
    flex-direction: column;
    align-items: center;
  }

  &__subtitle {
    margin-bottom: 8px;
  }

  &__title {
    text-align: center;

    @include small-tablet {
      text-align: initial;
    }
  }

  &__name {
    margin-bottom: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include small-tablet {
      align-items: start;
      align-self: start;
    }
  }

  &__list {
    margin-bottom: 50px;
    gap: 20px;
    width: 100%;
    height: 100%;
    align-self: start;
    overflow-x: auto;

    @include tablet {
      scroll-snap-type: y;

      &::-webkit-scrollbar {
        display: none;
      }
    }
  }

  &__item {
    position: relative;
    flex-direction: column;
    align-items: flex-end;
    background: url(../img/rate/cover-01.png) no-repeat;
    background-size: cover;
    height: 500px;
    min-width: 380px;
    border-radius: 32px;
    scroll-snap-align: start;

    &--02 {
      background: url(../img/rate/cover-02.png) no-repeat;
      background-size: cover;
    }

    &--03 {
      background: url(../img/rate/cover-03.png) no-repeat;
      background-size: cover;
    }

    @include small-tablet {
      min-width: 285px;
      height: 400px;
    }
  }

  &__stars {
    margin-top: 50px;
    padding: 4px 8px;
    align-items: center;
    border-radius: 4px 0px 0px 4px;
    background-color: rgba(253, 253, 253, 0.40);
    backdrop-filter: blur(2px);

    img {
      margin-right: 4px;
    }

    span {
      color: var(--main-color-btn);
      font-size: 14px;
      font-weight: 700;
      line-height: 128.571%;
    }
  }

  &__main {
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }

  &__label {
    color: var(--main-color-light);
    font-size: 24px;
    font-weight: 700;
    line-height: 120%;

    @include small-tablet {
      font-size: 18px;
    }
  }

  &__sublabel {
    color: var(--main-color-light);
    font-size: 14px;
    line-height: 128.571%;

    @include small-tablet {
      display: none;
    }
  }

  &__coast {
    padding: 8px 16px;
    border-radius: 4px;
    background: rgba(255, 255, 255, 0.30);
    color: var(--main-color-light);
    font-size: 20px;
    font-weight: 700;
    line-height: 120%;

    @include hover {
      background: rgba(255, 255, 255, 0.473);
    }

    @include small-tablet {
      padding: 6px 12px;
      font-size: 16px;
    }
  }

  &__showed {
    display: none;
  }

  &__description {
    color: var(--main-color-light);
    font-size: 16px;
    line-height: 150%;
    margin-bottom: 105px;
  }

  &__content {
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 20px 32px;
    width: 100%;
    border-radius: 0px 0px 32px 32px;
    background-color: rgba(255, 255, 255, 0.20);
    backdrop-filter: blur(3px);

    @include hover {
      .rate__showed {
        display: block;
      }
      .rate__main {
        margin-bottom: 38px;
      }
      padding: 30px 32px 20px 32px;
      min-height: 100%;
      border-radius: 32px;
    }

    @include small-tablet {
      padding: 24px;
    }
  }
}
