.find {
  min-height: 985px;
  background: url("../img/find/cover.png") center no-repeat;
  background-size: cover;
  padding-top: 551px;
  padding-bottom: 82px;

  min-height: 0;

  &__title {
    margin: 0;
    margin-bottom: 41px;
    grid-column: 11 span;
    color: var(--main-color-light);
    font-family: var(--second-family);
    font-size: 50px;
    font-weight: 700;
    line-height: 150%;
    letter-spacing: 1px;

    @include small-tablet {
      margin-bottom: 32px;
    }

    @include mobile {
      font-size: 28px;
    }
  }

  &__form {
    grid-column: 12 span;
  }
}

.form {
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.20);
  backdrop-filter: blur(3px);
  padding: 40px 42px;
  justify-content: space-between;

  @include tablet {
    justify-content: center;
    flex-direction: column;
  }

  @include small-tablet {
    padding: 0;
    background: none;
    backdrop-filter: none;
    justify-content: start;
  }


  &__wrapper {
    justify-content: space-between;
    width: 80%;

    @include tablet {
      width: 100%;
      justify-content: center;
    }
  }


  &__label {
    flex-direction: column;
    color: var(--main-color-light);
    font-size: 14px;
    font-weight: 400;
    line-height: 150%;
    width: 100%;
    max-width: 270px;
    margin-right: 15px;

    &:last-child {
      padding: 0;
    }

    @include notebook {
      margin-bottom: 25px;
    }

    @include small-tablet {
      display: none;
    }
  }

  &__input {
    display: flex;
    align-items: center;
    padding: 12px 16px;
    color: var(--main-color-light);
    font-size: 16px;
    font-weight: 700;
    line-height: 150%;
    background: none;
    border-radius: 8px;
    border: 1px solid var(--wl-primary-primary-300, #E1E3E4);
    margin-bottom: 8px;
  }


  &__btn {
    max-height: 52px;
    width: max-content;
    align-self: center;

    @include small-tablet {
      align-self: start;
    }
  }
}

.choices {
  display: flex;
  justify-content: space-between;
  max-width: 270px;
  margin-bottom: 8px;

  &[data-type*=select-one] .choices__inner {
    padding-bottom: 0;
  }

  &[data-type*=select-one]:after {
    top: 35%;
    border: none;
    width: 20px;
    height: 20px;
    background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 20 20" fill="none"><path d="M16.2475 6.6574L10 12.7474L3.7525 6.6574C3.64088 6.54837 3.49103 6.48733 3.335 6.48733C3.17897 6.48733 3.02912 6.54837 2.9175 6.6574C2.86345 6.71036 2.82052 6.77356 2.7912 6.84332C2.76189 6.91308 2.74679 6.98798 2.74679 7.06365C2.74679 7.13932 2.76189 7.21422 2.7912 7.28398C2.82052 7.35374 2.86345 7.41694 2.9175 7.4699L9.56375 13.9499C9.68047 14.0637 9.83701 14.1273 10 14.1273C10.163 14.1273 10.3195 14.0637 10.4362 13.9499L17.0825 7.47115C17.1369 7.41816 17.1802 7.35479 17.2097 7.2848C17.2393 7.21481 17.2545 7.13962 17.2545 7.06365C17.2545 6.98768 17.2393 6.91248 17.2097 6.8425C17.1802 6.77251 17.1369 6.70914 17.0825 6.65615C16.9709 6.54712 16.821 6.48608 16.665 6.48608C16.509 6.48608 16.3591 6.54712 16.2475 6.65615V6.6574Z" fill="%23FDFDFD"/></svg>') no-repeat;
    background-size: 20px;
    transition: transform 0.3s ease-in-out;
  }

  &[data-type*=select-one].is-open:after {
    transform: rotate(180deg);
    margin-top: -4px;
  }

  &__inner {
    display: flex;
    align-items: center;
    padding: 0 16px;
    color: var(--main-color-light);
    font-size: 16px;
    font-weight: 700;
    line-height: 150%;
    background: none;
    border-radius: 8px;
    border: 1px solid var(--wl-primary-primary-300, #E1E3E4);
  }

  &__list {

    &--single {
      padding: 14px 0;
    }

    &--dropdown {
      border-radius: 10px;
      background: rgba(122, 122, 122, 0.74);
      backdrop-filter: blur(100px);
    }
  }

  &__list--dropdown &__item--selectable.is-highlighted {
    background: var(--main-color-btn);
  }

  &__item--disabled {
    display: none;
  }
}


